.rotated{
    border: 10px solid #fff;
   transform:rotateZ(-25deg);
    margin-top: 150px;
    margin-left: 150px;
    box-shadow: 20px 20px 20px 20px  gray;
}
.rotate-container{
    width: 500px;
    height: 500px;
}

.container-fluid{
    padding: 0;
}

.header-h{
    background-color: cadetblue;
    max-height: fit-content;
    overflow: hidden;
}

.straight{
    background-color: rgba(255, 255, 255, 0.637);
    background-size: inherit;
    position: absolute;
    left: 40%;
    top: 40%;
}