
.sec{
    background-color: slategray;
    position: relative;
    max-height: max-content;
    overflow: hidden;
}

.rotated-right{
    transform: rotateZ(25deg);
    margin-top: 100px;
    position: relative;
    float: right;
    right: -70%;
    border: 10px solid #fff;
    box-shadow :20px 20px 20px 20px rgba(156, 149, 149, 0.767);
    margin-bottom: 150px;
    
}

.straight-left{
    position: absolute;
    top: 40%;
    left: 10%;
    z-index: 1;
    background-color: rgba(255, 255, 255, 0.63);
    text-align: center;
}